import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { CartListComponent } from './cart-list.component';
import { LoderModule } from 'src/app/common/loder/loder.module';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';
import { CartService } from 'src/app/service/cart.service';

const routes: Routes = [
  {
    path: '',
    component: CartListComponent,
    // data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../cart-add/cart-add.module').then(m => m.CartAddModule),
    // data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../cart-add/cart-add.module').then(m => m.CartAddModule),
    // data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    CartListComponent
  ],
  imports: [
    CommonModule,
    LoderModule,
    PaginationModule,
    RouterModule.forChild(routes)
  ],
  providers: [
    CartService
  ]
})
export class CartListModule { }